import Vue from 'vue'
import VueRouter from 'vue-router'
import Index from '../views/blog/index'
import Diary from '../views/Diary'
import vue插槽 from '../views/Diary/vue插槽'
import vue组件传值 from '../views/Diary/vue组件传值'
Vue.use(VueRouter)
//配置路由
  const routes = [
  {
    path:'/',
    redirect:'/home'
  },
  {
    path: '/home',
    name: 'Home',
    component: ()=> import('../views/Home')
  },
  {
    path: '/about',
    name: 'About',
    component: () => import( '../views/about')
  },
    {
      path: '/blog',
      name: 'Blog',
      // component: () => import( '../views/blog/index')
      component: Index
    },
    {
      path: '/message',
      name: 'Message',
      component: () => import( '../views/Message')
    },
    {
      path: '/link',
      name: 'Link',
      component: () => import( '../views/link')
    },
    {
      path:'/diary',
      name: 'Diary',
      component:Diary,
      children:[
        // {
        //         //   path:'/diary',
        //         //   redirect:'/diary/vue插槽'
        //         // },
        {
          path:'vue插槽',  //指向当前路由 /diary
          component:()=>import('../views/Diary/vue插槽')
        },
        {
          path:'vue组件传值',
          component:()=>import('../views/Diary/vue组件传值')
        }
      ]
    },
]
//实列77n 化路由
const router = new VueRouter({
  routes
})
//导出路由
export default router
