<template>
    <div class="infinite-list-wrapper" style="overflow:auto">
        <ul
                class="list"
                v-infinite-scroll="load"
                infinite-scroll-disabled="disabled"
        >
            <li v-for="i in count" class="list-item">
                <h5>
                    <span class="yuan">[原创]</span>
                    <span class="fen">模板分享</span>
                </h5>
                <div class="time">
                    <span class="day">22</span>
                    <span class="fs-18 month">6 <span class="fs-14">月</span></span>
                    <span class="year fs-18">2020</span>
                </div>
                <div class="content">
                    <a href="" class="cover img-light">
                        <img src="../assets/img/1.jpg" alt="">
                    </a>
                    <p>重新整理了下模板（首页，文章列表，阅读文章，留言板，友链）   </p>
                </div>
                <div class="read-more">
                    <a href="" class="fs-wb">继续阅读</a>
                </div>
                <div class="footer">
                    <div class="tags">
                        <i class="el-icon-edit"></i>
                        <span>个人日记</span>
                    </div>
                    <div class="f-fr">
                        <i class="el-icon-view "></i>
                        <span>1941</span>
                        <i class="el-icon-chat-line-round fr"></i>
                        <span>7</span>
                    </div>

                </div>
            </li>
        </ul>
        <p v-if="loading">加载中...</p>
        <p v-if="noMore">没有更多了</p>
    </div>
</template>

<script>
    export default {
        name: "ArticalShow",
        data () {
            return {
                count: 4,
                loading: false
            }
        },
        computed: {
            noMore () {
                return this.count >= 7
            },
            disabled () {
                return this.loading || this.noMore
            }
        },
        methods: {
            load () {
                this.loading = true
                setTimeout(() => {
                    this.count += 2
                    this.loading = false
                }, 4000)
            }
        }
    }
</script>

<style scoped lang="less">
    .fs-18{
        font-size: 18px;
    }
    .infinite-list-wrapper{
        ul{
            li{
                padding: 20px 30px 25px 30px;
                position: relative;
                margin-top: 20px;
                background-color: #fff;
                h5{
                    line-height: 30px;
                    padding: 15px 130px 5px 0px;
                    border-bottom: 1px solid #e8e9e7;
                    font-size: 18px;
                    font-weight: 400;
                    .yuan{
                        font-size: 16px;
                        font-weight: 400;
                        display: inline-block;
                        vertical-align: bottom;
                        margin-right: 5px;
                        color: #2ea7e0;
                    }
                    .fen{
                        color: #212220;
                    }
                }
                .time{
                    position: absolute;
                    right: 10px;
                    top: 10px;
                    background-color: #fff;
                    padding: 0 20px 5px 20px;
                    line-height: 32px;
                    .day{
                        display: block;
                        text-align: center;
                        font-weight: 700;
                        font-size: 40px;
                        color: #6bc30d;
                        position: relative;
                        top: 2px;
                    }
                    .month{
                        color: #989997;
                        .fs-14{
                            font-size: 14px;
                        }
                        margin-right: 10px;
                    }
                    .year{
                        color: #989997;
                    }

                }
                .content{
                    display: flex;
                     a{
                         color: #3e8bc7;
                         margin: 0 2px;
                     }
                    .cover{
                        display: block;
                        width: 300px;
                        height: 180px;
                        border: 1px solid #e8e9e7;
                        overflow: hidden;
                        margin-right: 20px;
                        img{
                            display: block;
                            width: 100% !important;
                            height: 100% !important;
                        }
                    }
                }
                .read-more{
                    height: 40px;
                    line-height: 40px;
                    position: relative;
                    &::before{
                        content: " ";
                        display: inline-block;
                        height: 1px;
                        top: 20px;
                        left: 100px;
                        right: 0;
                        position: absolute;
                        background-color: #d0d0d0;
                    }
                   .fs-wb{
                       font-weight: bold;
                       color:#383937 ;
                   }
                }
                .footer{
                    display: flex;
                    justify-content: space-between;
                    width: 100%;
                    height: 20px;
                    background-color: #ffffff;
                    margin-top: 5px;
                    .el-icon-edit{
                        font-size: 20px;
                        margin-right: 8px;
                    }
                    span{
                        display: inline-block;
                        height: 20px;
                        font-size: 14px;
                        background-color: #f1f2f0;
                        color: #787977;
                        vertical-align: 3px;
                        cursor: pointer;
                        transition: background-color .2s;
                        margin-left: 5px ;
                        &:hover{
                            background-color: #6bc30d;
                        }
                    }
                    .f-fr{
