<template>
    <div id="nav">
        <div class="nav-main">
            <div class="nav-main-logo">liquan</div>
            <div class="n-m-login">
                <el-button type="primary" @click="handLogin">登陆</el-button>
                <el-button type="success" @click="handRegister">注册</el-button>
            </div>
            <div class="n-m-nav">
                <ul :class="'list'+whichActive">
                    <li><router-link to="/">首页</router-link></li>
                    <li><router-link to="/blog">博客</router-link></li>
                    <li><router-link to="/message">留言</router-link></li>
                    <li><router-link to="/diary">日记</router-link></li>
                    <li><router-link to="/link">友链</router-link></li>
                    <li><router-link to="/about">关于</router-link></li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
    import Register from './Register'
    import Login from './Login'
    export default {
        name: "Nav",
        components:{
            Register,
            Login,
        },
        data(){
            return{
                routerList:["Home","Blog","Message","Diary","Link","About"]
            }
        },
        computed:{
            whichActive(){
                let index = this.routerList.indexOf(this.$route.name)
                return index +1;
            }
        },
        methods:{
            handRegister(){
                const h = this.$createElement;
                this.$msgbox({
                    title: '注册',
                    message: h(Register),
                    showCancelButton: false,
                    showConfirmButton:false,
                    closeOnClickModal:false,
                    beforeClose: (action, instance, done) => {
                        if (action === 'confirm') {
                            instance.confirmButtonLoading = true;
                            instance.confirmButtonText = '执行中...';
                            setTimeout(() => {
                                done();
                                setTimeout(() => {
                                    instance.confirmButtonLoading = false;
                                }, 300);
                            }, 3000);
                        } else {
                            done();
                        }
                    }
                }).then(() => {});
            },
            handLogin(){
                const h = this.$createElement;
                this.$msgbox({
                    title: '登陆',
                    message: h(Login),
                    showCancelButton: false,
                    showConfirmButton:false,
                    closeOnClickModal:false,
                    beforeClose: (action, instance, done) => {
                        if (action === 'confirm') {
                            instance.confirmButtonLoading = true;
                            instance.confirmButtonText = '执行中...';
                            setTimeout(() => {
                                done();
                                setTimeout(() => {
                                    instance.confirmButtonLoading = false;
                                }, 300);
                            }, 3000);
                        } else {
                            done();
                        }
                    }
                }).then(() => {});
            }
        },

    }
</script>

<style scoped lang="less">
    #nav{
        z-index: 999;   //导航条的层级是最高的
        height: 60px;
        background-color: #fff;
       >.nav-main{
           width: 100%;
           max-width: 1360px;
           height: 60px;
           margin: 0 auto;
           padding: 0 50px;
           .nav-main-logo{
               float: left;
               width: 150px;
               height: 60px;
               line-height: 60px;
               text-align: left;
               font-family: BarbaraHand;
               font-size: 40px;
               color: #777;
           }
           .n-m-login{
               float: right;
               height: 60px;
               line-height: 60px;
               el-button{
                   padding: 9px 13px;
               }
           }
           .n-m-nav{
               margin-right: 8%;
               float: right;
               width: 576px;
               height: 60px;
               ul{
                    display: flex;
                    width: 100%;
                    height: 100%;
                   li{
                       position: relative;
                       flex: 1;
                       a{
                           display: block;
                           width: 100%;
                           height: 100%;
                           text-align: center;
                           line-height: 60px;
                           font-weight: 400;
                           font-size: 15px;
                           color: #515250;
                           transition: color .5s;
                           &::after{
                               position: absolute;
                               content: "";
                               bottom: 0;
                               left: 0;
                               right: 0;
                               margin: auto;
                                width: 0;
                               height: 2px;
                               background-color: #6bc30d;
                               transition: width .5s;
                           }
                           &:hover{
                               color: #6bc30d;
                               &:after{
                                   width: 100%;
                               }
                           }
                       }
                   }
                   &.list1 li:nth-child(1),
                   &.list2 li:nth-child(2),
                   &.list3 li:nth-child(3),
                   &.list4 li:nth-child(4),
                   &.list5 li:nth-child(5),
                   &.list6 li:nth-child(6)
                   {
                       a{
                           color: #6bc30d;
                           &::after{
                               width: 100%;
                           }
                       }

                   }
               }
           }
       }
    }
</style>